<template>
  <div class="accept-paper">
    <div class="paper">
      <h1 class="title">Special Issues</h1>
      <p>Presented papers will be considered for publication in some Special Issues in International Journals.</p>
      <li>Journal of System Architecture</li>
      <li>Security and Communication Networks</li>
      <li>Journal of Information Security and Applications</li>
      <li>Applied Science</li>
      <p>more to be announced later</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'AcceptPaper'
  }
</script>

<style lang="stylus" scoped>
  .accept-paper
    margin 10px auto
    box-sizing border-box
    border 1px solid #ccc
    width 70%;
    background-color #ddd
    border-radius 4px
    padding 10px
    .paper
      .title
        text-align center
        font-size 24px
        padding 10px 0
      P, li
        line-height 1.8
        margin 5px 0
        font-size 18px
</style>


